import React from 'react';

export default function DictView({ items = [] }) {
    return (
        <div className="dict-list">
            <h2>Dictionary</h2>
            {items && items.map((item, i) => {
                <div key={`${item.title}_${i}`}>
                    <p>{item.title}</p>
                    <p>{item.description}</p>
                </div>;
            })}
        </div>
    );
}