import React, { useEffect, useState } from 'react';
import './App.css';
import DictView from './components/DictView';
import api from './api';

function App() {
  const [items, setItems] = useState([])
  useEffect(() => api().get('/dict').then(setItems), [])

  return (
    <div className="App">
      <header className="App-header">
        bogdanovsi
      </header>
      <DictView items={items}/>
    </div>
  );
}

export default App;
